import React, { Component } from "react";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import "react-bootstrap-table/dist/react-bootstrap-table.min.css";
class InActivePasswordTable extends Component {
  render() {
    const sortingOptions = {
      defaultSortName: "CreatedOn",
      noDataText: "No records found for inactive passwords",
      defaultSortOrder: "asc",
      sizePerPageList: [
        {
          text: "5",
          value: 5
        },
        {
          text: "10",
          value: 10
        },
        {
          text: "20",
          value: 20
        },
        {
          text: "All",
          value: this.props.passwords.length
        }
      ],
      sizePerPage: 5
    };
    return (
      <BootstrapTable
        ref="table"
        data={this.props.passwords}
        options={sortingOptions}
        search={true}
        pagination = {this.props.passwords.length > 0 ? true : false}
        hover={true}
      >
        <TableHeaderColumn dataField="Id" headerAlign="left" isKey hidden>
          Id
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="Password"
          headerAlign="left"
          width="40"
          dataSort={true}
        >
        Password
        </TableHeaderColumn>
        <TableHeaderColumn dataField="CreatedOn" headerAlign="left" hidden>
        CreatedOn
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="delete"
          dataFormat={this.props.onDelete}
          headerAlign="left"
          width="20"
          export={false}
        >
          Activate
        </TableHeaderColumn>
      </BootstrapTable>
    );
  }
}

export default InActivePasswordTable;



