import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import CardLayout from "../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Row } from "reactstrap";
import _ from "lodash";
import Loader from "../../components/Loader/Loader";
import ConfirmModal from "../../components/Modal/ConfirmModal";
import * as constants from "../../constants/StatusConstants";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
import DropdownSelect from "../../components/InputElement/Dropdown";
import ActivePasswordTable from "./ActivePasswordTable";
import InActivePasswordTable from "./InActivePasswordTable";
import { Link } from "react-router-dom";
import MessageModel from "../../components/Modal/MasterModal";

class MasterPassword extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      tableStatus: true,
      modalStatus: false,
      passwordToDelete: {},
      messageModelStatus: false,
      message: ""
    };
  }

  componentDidMount() {
    this.props.getMasterPasswords();
    setTimeout(() => {
      this.setState({
        loading: false
      });
    }, 1000);
  }
  onTablestatusChange(value) {
    if (value != null) {
      this.setState({
        tableStatus: value
      });
    }
  }
  onDeletePassword(cell, row) {
    if (this.state.tableStatus) {
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-trash" title="Deactivate" />
        </Link>
      );
    } else {
      return (
        <Link to={this} onClick={() => this.onModalToggleActive(row)}>
          <i className="fa fa-check-square-o" title="Activate" />
        </Link>
      );
    }
  }
  onModalToggleActive(row) {
    this.props.getMasterPasswords();
    setTimeout(()=>{
      if (this.props.activeMasterPasswordsList.length > 0) {
        this.setState({
          message:
            "One active record already exists. Please delete the existing active record to activate."
        });
        this.onMessageModalToggle();
      } else {
        this.onModalToggle(row);
      }
    },1000);
  
  }
  onMessageModalToggle() {
    this.setState({ messageModelStatus: !this.state.messageModelStatus });
  }
  onModalToggle(password) {
    this.setState({
      passwordToDelete: password,
      modalStatus: !this.state.modalStatus
    });
  }
  onConfirmDelete() {
    let passwordToDelete = { ...this.state.passwordToDelete };
    let message = "";
    if (this.state.tableStatus) {
      passwordToDelete.Active = false;
      message = "Question deactivated successfully";
    } else {
      passwordToDelete.Active = true;
      message = "Question activated successfully";
    }
    this.props.deleteMasterPasswords(passwordToDelete.Id, passwordToDelete);
    this.setState({ loading: true });
    let compRef = this;
    setTimeout(() => {
      compRef.props.masterPasswordError
        ? (message = "Something went wrong !")
        : message;
      compRef.setState({ loading: false });
      Toaster.Toaster(message, compRef.props.masterPasswordError);
    }, 2000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  onAddPassword() {
    this.props.getMasterPasswords();
    setTimeout(()=>{
      if (this.props.activeMasterPasswordsList.length > 0) {
        this.setState({
          message: "Please delete the existing active record to add new password."
        });
        this.onMessageModalToggle();
      } else {
        this.props.history.push("/configureMasterPassword/masterPasswordForm");
      }
    },1000);
  }
  render() {
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Master Password"
        buttonName="Add Password"
        buttonClick={this.onAddPassword.bind(this)}
        buttonLink={this}
      >
        <Row className="address-drop-margin">
          <Col xs="12" md="10" />
          <Col md="2">
            <DropdownSelect
              label="Status"
              options={constants.tableStatus}
              value={this.state.tableStatus}
              onChange={this.onTablestatusChange.bind(this)}
              search={false}
              clear ={false}
              simpleValue
            />
          </Col>
        </Row>
        <FormGroup row>
          <Col xs="12">
            {this.state.tableStatus ? (
              <ActivePasswordTable
                passwords={this.props.activeMasterPasswordsList}
                onDelete={this.onDeletePassword.bind(this)}
              />
            ) : (
              <InActivePasswordTable
                passwords={this.props.inActiveMasterPasswordsList}
                onDelete={this.onDeletePassword.bind(this)}
              />
            )}
          </Col>
        </FormGroup>
        <ConfirmModal
          isOpen={this.state.modalStatus}
          onModalToggle={this.onModalToggle.bind(this)}
          onConfirmDelete={this.onConfirmDelete.bind(this)}
          title={this.state.tableStatus ? "Deactivate" : "Activate"}
          message={
            this.state.tableStatus
              ? "Are you sure you want to deactivate this password record ?"
              : "Are you sure you want to activate this password record ?"
          }
        />
        <MessageModel
          title="Message"
          openFlag={this.state.messageModelStatus}
          toggleFunction={this.onMessageModalToggle.bind(this)}
          message={this.state.message}
        />

        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeMasterPasswordsList:
      state.masterPasswordReducer.activeMasterPasswordsList,
    inActiveMasterPasswordsList:
      state.masterPasswordReducer.inActiveMasterPasswordsList,
    masterPasswordError: state.masterPasswordReducer.masterPasswordError
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getMasterPasswords: () => dispatch(actions.getMasterPasswords()),
    deleteMasterPasswords: (id, masterPassword) =>
      dispatch(actions.deleteMasterPasswords(id, masterPassword))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(MasterPassword);



