import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import CardLayout from "../../components/Cards/CardLayout";
import { FormGroup, Col, Button } from "reactstrap";
import InputElement from "../../components/InputElement/InputElement";
import _ from "lodash";
import Loader from "../../components/Loader/Loader";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
import MessageModel from "../../components/Modal/MasterModal";
class MasterPasswordForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      password: {
        Password: "",
        PasswordRequired: false,
        PasswordInvalid: false,
        Active: true,
        CreatedOn: "",
        CreatedBy: ""
      },
      inputType: "password",
      showPassword: false,
      messageModelStatus: false
    };
  }

  componentDidMount() {
    setTimeout(() => {
      if (this.props.activeMasterPasswordsList.length > 0) {
        this.props.history.push("/configureMasterPassword");
      } else {
        this.setState({
          loading: false
        });
      }
    }, 1000);
  }

  onChangeHandler(event) {
    let password = { ...this.state.password };
    password[event.target.name] = event.target.value;
    password[event.target.name + "Required"] = false;
    password[event.target.name + "Invalid"] = false;
    this.setState({ password });
  }
  onMessageModalToggle() {
    this.setState({ messageModelStatus: !this.state.messageModelStatus });
  }
  onSubmit() {
    this.props.getMasterPasswords();
    setTimeout(() => {
      if (this.props.activeMasterPasswordsList.length > 0) {
        this.setState({
          message:
            "One active record already exists. Please delete the existing active record to activate."
        });
        this.onMessageModalToggle();
      } else {
        let password = { ...this.state.password };
        if (this.passwordExists(password)) {
          this.setState({
            message:
              "The password entered already exists among the inactive passwords. Please activate from inactive passwords for same master password"
          });
          this.onMessageModalToggle();
        } else {
          if (
            password.Password &&
            password.Password.trim().length > 0 &&
            /^\d+$/.test(password.Password.trim()) &&
            password.Password.trim().length === 6
          ) {
            password.CreatedBy = localStorage.getItem("user");
            password.CreatedOn = new Date();
            let masterPassword = _.pick(password, [
              "Password",
              "Active",
              "CreatedBy",
              "CreatedOn"
            ]);
            this.props.addMasterPasswords(masterPassword);
            this.setState({ loading: true });
            let compRef = this;
            let message = "Master password created successfully";
            setTimeout(() => {
              compRef.props.masterPasswordError
                ? (message = "Something went wrong !")
                : message;
              compRef.setState({ loading: false });
              compRef.onReset();
              Toaster.Toaster(message, compRef.props.masterPasswordError);
              setTimeout(() => {
                if (!compRef.props.masterPasswordError) {
                  compRef.props.history.push("/configureMasterPassword");
                }
              }, 1000);
            }, 2000);
          } else {
            let valid =
              /^\d+$/.test(password.Password.trim()) &&
              password.Password.trim().length === 6;
            if (!valid) {
              password.PasswordInvalid = true;
            }
            if (!password.Password || !password.Password.trim().length > 0) {
              password.PasswordInvalid = false;
              password.PasswordRequired = true;
            }
            this.setState({ password });
          }
        }
      }
    }, 1000);
  }
  passwordExists(password) {
    let passwordAlreadyExists = _.find(this.props.inActiveMasterPasswordsList, {
      Password: password.Password
    });
    if (passwordAlreadyExists) {
      return true;
    } else {
      return false;
    }
  }
  onReset() {
    this.setState({
      password: {
        Password: "",
        PasswordRequired: false,
        PasswordInvalid: false,
        Active: true,
        CreatedOn: "",
        CreatedBy: ""
      },
      inputType: "password",
      showPassword: false
    });
  }
  setInputToNumeric(e) {
    const re = /[0-9]+/g;
    if (!re.test(e.key)) {
      e.preventDefault();
    }
  }
  render() {
    const password = { ...this.state.password };
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Master Password Form"
        navigation={true}
        navigationRoute="/configureMasterPassword"
      >
        <div className="div-padding">
          <FormGroup row />
          <FormGroup row>
            <Col xs="8" md="4">
              <InputElement
                isPassword={true}
                type={this.state.inputType}
                label="Master password"
                placeholder="Master password should be 6 digits"
                maxLength={6}
                name="Password"
                required={password.PasswordRequired}
                invalid={password.PasswordInvalid}
                value={password.Password}
                onChange={event => this.onChangeHandler(event)}
                showPassword={this.state.showPassword}
                onKeyPress={e => this.setInputToNumeric(e)}
                onEyeToggle={() => {
                  this.setState({
                    showPassword: !this.state.showPassword,
                    inputType:
                      this.state.inputType === "password" ? "input" : "password"
                  });
                }}
              />
            </Col>
          </FormGroup>

          <FormGroup row>
            <Col md="1">
              <Button
                className="theme-positive-btn"
                onClick={this.onSubmit.bind(this)}
              >
                Create
              </Button>
            </Col>
            <Col md="1">
              <Button
                className="theme-reset-btn"
                onClick={this.onReset.bind(this)}
              >
                Reset
              </Button>
            </Col>
          </FormGroup>
          <ToastContainer autoClose={1000} />
          <MessageModel
            title="Message"
            openFlag={this.state.messageModelStatus}
            toggleFunction={this.onMessageModalToggle.bind(this)}
            message={this.state.message}
          />
        </div>
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeMasterPasswordsList:
      state.masterPasswordReducer.activeMasterPasswordsList,
    inActiveMasterPasswordsList:
      state.masterPasswordReducer.inActiveMasterPasswordsList
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getMasterPasswords: () => dispatch(actions.getMasterPasswords()),
    addMasterPasswords: password =>
      dispatch(actions.addMasterPasswords(password))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(MasterPasswordForm);



