import React, { Component } from "react";
import { Route } from "react-router-dom";
import MasterPassword from "./MasterPassword";
import MasterPasswordForm from "./MasterPasswordForm";

export default class MasterPasswordModule extends Component {
  render() {
    return (
      <div>
        <Route exact path={this.props.match.path} component={MasterPassword} />
        <Route
          path={`${this.props.match.path}/masterPasswordForm/:id?`}
          component={MasterPasswordForm}
        />
      </div>
    );
  }
}



